	module.exports = {"data":{"wordpressPost":{"title":"Painkillers linked to heightened irregular heartbeat risk in older adults","content":"<p><a href=\"http://bmjopen.bmj.com/lookup/doi/10.1136/bmjopen-2013-004059\" target=\"_blank\">Current and recent use of painkillers/anti-inflammatories may be linked to a heightened risk of an irregular heartbeat (atrial fibrillation) among older adults</a>, finds a large population study published in <a href=\"http://bmjopen.bmj.com/\" target=\"_blank\">BMJ Open.</a></p>\n<p>Atrial fibrillation has itself been linked to stroke, heart failure, and reduced life expectancy, while previously published research has linked the use of non-steroidal anti-inflammatory drugs, or NSAIDs, to a heightened risk of cardiovascular problems, including heart attack.</p>\n<p>The research team regularly monitored the heart health of 8423 people taking part in the Rotterdam Study, a population based study that has been tracking the development of ill health and associated risk factors among adults aged 55 and older since 1990 in one district of Rotterdam, The Netherlands.</p>\n<p>New and current cases of atrial fibrillation were diagnosed using heart tracer recordings (ECGs) while details of drugs prescribed to study participants was gathered from pharmacies collaborating on the research project.</p>\n<p>The average of the study participants was 68.5, and over half of them (58%) were women.</p>\n<p>During the average monitoring period, which spanned just under 13 years, 857 of the 8423 participants developed atrial fibrillation, 261 of whom had never used NSAIDs when they were diagnosed, while 554 had used NSAIDs in the past, and 42 were currently taking these drugs.</p>\n<p>Current use was associated with a 76% greater risk of atrial fibrillation than never use, after taking account of other risk factors, such as age, sex, and underlying cardiovascular problems.</p>\n<p>Similarly, recent use (with the preceding 30 days) of these drugs was linked to an 84% greater risk of atrial fibrillation. While there was a trend for higher doses to be linked to a correspondingly higher risk, this trend was not statistically significant.</p>\n<p>NSAIDs may contribute to atrial fibrillation because they inhibit the production of the enzyme cyclo-oxygenase, which may increase blood pressure as a result of fluid retention, suggest the authors.</p>\n<p>Alternatively, use of NSAIDs may indicate underlying inflammation, which may increase the risk of atrial fibrillation, they say.</p>\n<p>But whatever the explanation, “the underlying mechanism behind this association [between NSAIDs and atrial fibrillation] deserves further attention,” they conclude.<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"af6635c1-6710-504c-b140-bf5dd31863fd"}}

